@import url(https://cdn.jsdelivr.net/gh/aymanbagabas/iosevka-fonts@v6.1.2/dist/iosevka/iosevka.min.css);
body {
  margin: 0;
  font-family:  'Iosevka Web', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about{max-width:60vw;margin:5rem auto 0;min-height:50vh}.about h2,.about h3{color:#051622;background-color:#deb992;display:inline-block;padding:.1rem .3rem;border-radius:.2rem}
.project-preview{border:1px solid #051622;background-color:#03121d;max-width:20%;min-height:10vh;padding:.5rem;-webkit-flex-grow:1;flex-grow:1;border-radius:5px;color:inherit;text-decoration:none;overflow:auto}.project-preview .project-name{border-bottom:1px solid;font-weight:bold;padding:5px;border-top-left-radius:5px;border-top-right-radius:5px}.project-preview:hover{border-color:inherit;color:#051622;background-color:#deb992}.project-preview:hover .project-name{color:#deb992;background-color:#051622}@media screen and (max-width: 800px){.project-preview{min-width:40%}}@media screen and (max-width: 420px){.project-preview{min-width:80%}}
.projects-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:1rem 3rem;-webkit-justify-content:space-between;justify-content:space-between;min-height:65vh}.projects-container .filter-container{border:1px solid;position:relative;padding:2rem 1rem}.projects-container .filter-container .title{position:absolute;top:-2rem;border-radius:5px;padding:5px;color:#051622;background-color:#deb992}.projects-container .filter-container .filter-type-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around}.projects-container .filter-container .filter-type-container .filter{cursor:pointer;padding:5px;border-radius:5px}.projects-container .filter-container .filter-type-container .filter.selected{color:#051622;background-color:#deb992}.projects-container .filter-container .filter-type-container .filter:hover{color:#051622;background-color:#deb992}.projects-container .project-preview-container{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-gap:1rem;gap:1rem;-webkit-justify-content:center;justify-content:center;border:1px solid;min-height:40vh;min-width:30%;position:relative;padding:2rem 1rem}.projects-container .project-preview-container .title{position:absolute;top:-2rem;border-radius:5px;padding:5px;color:#051622;background-color:#deb992}@media screen and (max-width: 800px){.projects-container{padding:1rem}.projects-container .project-preview-container{padding:2rem 0}}
.project-page{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:1rem auto;max-width:70%;-webkit-align-self:center;align-self:center;background-color:#0c1f2c;border-radius:.5rem}.project-page .title{text-align:center}.project-page .description{text-align:center}.project-page .description .link_container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;background-color:#03121d;padding:1rem;border-radius:.5rem}.project-page .description .link_container a{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;grid-gap:1rem;gap:1rem;text-decoration:none;color:inherit;padding:1rem;border-radius:.5rem}.project-page .description .link_container a:hover{background-color:#0c1f2c}.project-page .description .link_container a:hover svg path{fill:#deb992}.project-page .description .link_container svg{width:2rem}.project-page .description .tags{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;grid-gap:.5rem;gap:.5rem;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-align-items:center;align-items:center}.project-page .description .tags span{background-color:#03121d;padding:.5rem 1rem;border-radius:.5rem}.project-page .markdown{-webkit-align-self:center;align-self:center;background-color:#03121d;min-width:60%;padding:1rem;border-radius:.5rem;max-width:120%;overflow:auto}.project-page .markdown img{max-width:90%}

.footer{height:10vh;display:-webkit-flex;display:flex;width:100vw;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border-top:1px solid;margin-top:3rem}
.header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;height:10vw;-webkit-align-items:center;align-items:center;vertical-align:middle;grid-gap:2rem;gap:2rem;padding:0 5rem}.header .title-container{font-size:2rem;border:1px solid;padding:.5rem;box-shadow:0 0 6px}.header span{cursor:pointer}.header .options{-webkit-flex-grow:1;flex-grow:1;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-end;justify-content:flex-end;grid-gap:2rem;gap:2rem}.header .options .option{color:#deb992;text-decoration:none;padding:5px;border-radius:5px}.header .options .option:hover{color:#051622;background-color:#deb992}@media screen and (max-width: 800px){.header{padding:1rem}}@media screen and (max-width: 525px){.header{height:30vw;-webkit-flex-direction:column;flex-direction:column}}
body {
  background: #051622; /*000C66;*/
  color: #DEB992;
}

a {
  color: #1BA098;
}
